body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  font-family: Arial, sans-serif;
  font-size: 100px;
  color: white;
}

.block_item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: 33%;
  min-width: 300px;
}

.block-1 {
  order: 1;
  background-color: rgb(100%, 0%, 0%);
}

.block-2 {
  order: 2;
  background-color: rgb(80%, 0%, 0%);
}

.block-3 {
  order: 3;
  background-color: rgb(60%, 0%, 0%);
}

.block-4 {
  order: 4;
  background-color: rgb(40%, 0%, 0%);
}

.block-5 {
  order: 5;
  background-color: rgb(20%, 0%, 0%);
}

.block-6 {
  order: 6;
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) and (max-width: 899px) {
  .block_item {
    flex-basis: 300px;
  }

  .block-3 {
    order: 4;
  }

  .block-4 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block-4 {
    order: 6;
  }

  .block-6 {
    order: 4;
  }
}
